.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.75);
    overflow: hidden;
    overflow-y: auto;
    transition: opacity 0.3s;
    z-index: 999;

    &__wrapper {
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        min-height: 100%;
        padding: 0px 2px;
    }

    &__content {
        text-align: center;
        position: relative;
        width: 100%;
        max-width: 1010px;
        border-radius: 20px;
        background-color: white;
        color: black;
        padding: 40px 20px 20px 20px;
        transform: translateY(-50px);
        transition: opacity 0.3s, transform 0.9s;
        // max-height: 500px;
        margin-top: 120px;
    }

    &__close-button {
        position: absolute;
    top: 16px;
    right: 16px;
    width: 30px;
    height: 30px;
    padding: 0;
    background-color: transparent;
    }
}